.Burger {
    width: 100%;
    height: 450px;
    overflow: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 1.25rem;
    margin: 0 auto;
}
@media (min-width:1000px) and (min-height:600px){
  .Burger{
    width:800px;
    height: 500px;
  }
}
@media (min-width:500px) and (min-height:401px){
  .Burger{
    width:450px;
    height: 400px ;
  }
}
@media (min-width:500px) and (min-height:400px){
  .Burger{
    width:350px;
    height: 300px;
  }
}